html {
  font-size: 62.5%;
  box-sizing: border-box;
  user-select: none;
}

*, *::before, *::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "PT Sans", sans-serif;
}

a {
  text-decoration: none;
}
a:hover {
  cursor: pointer;
}

.contenedor {
  width: 95%;
  margin: 0 auto;
}

button:hover {
  cursor: pointer;
}

.boton {
  color: #000;
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
  font-size: 1.8rem;
  padding: 1rem 3rem;
  border-radius: 2rem;
}

.boton_verde {
  background-color: #6abf4b;
  transition: background-color 0.2s ease; /* Duración y tipo de transición */
}

.boton_verde:hover {
  background-color: #a4dd8f;
}

.footer {
  background-color: #292929;
  padding: 2rem;
}
.footer p {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  color: #adb5bd;
}

.topbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.topbar img {
  width: 10rem;
}
.topbar .navegacion {
  display: flex;
  flex-direction: row;
  column-gap: 2rem;
  margin-right: 2rem;
}
.topbar .topbar_derecha a {
  color: #212529;
  font-size: 2rem;
}

.banner {
  max-width: 150rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  row-gap: 5rem;
  align-items: center;
}
@media (min-width: 996px) {
  .banner {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    column-gap: 5rem;
  }
}

.banner_izquierda h2 {
  line-height: 7rem;
  font-family: "PT Sans", sans-serif;
  font-weight: 300;
  font-size: 5.4rem;
}
.banner_izquierda p {
  font-family: "PT Sans", sans-serif;
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 1.2rem;
}
.banner_izquierda p:first-of-type {
  margin-top: 2rem;
}
.banner_izquierda p:last-of-type {
  margin-bottom: 4rem;
}

.estadisticas {
  max-width: 140rem;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  column-gap: 4rem;
  margin-top: 10rem;
}

.estadistica {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: 1px solid #dddddd;
  padding: 1rem 2rem;
}
.estadistica .estadistica_titulo {
  font-family: "PT Sans", sans-serif;
  font-size: 2.6rem;
  text-align: center;
  color: #333333;
  font-weight: bold;
  margin-bottom: 1.2rem;
}
.estadistica .estadistica_descripcion {
  font-family: "PT Sans", sans-serif;
  font-size: 1.4rem;
  text-align: center;
  color: #6c757d;
}

.memorial {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10rem;
  padding-bottom: 10rem;
}
.memorial p {
  font-size: 1.6rem;
  font-family: "PT Sans", sans-serif;
}
.memorial p:first-of-type {
  font-family: "PT Sans", sans-serif;
  font-size: 4rem;
  max-width: 80rem;
}
.memorial p {
  text-align: center;
  line-height: 5rem;
}
.memorial i {
  font-size: 5rem;
  color: #8ccc75;
}